<template>
  <CheckboxBase>
    <input type="checkbox" 
      :id="id" 
      :value="id" 
      v-model="input" 
      :disabled="isDisabled">
    <label :class="{ir_pm : text == undefined}" :for="id">
      <span class="txt_checkbox">{{ text }}</span>
    </label>
    <span class="ico_zippro ico_checkbox"></span>
  </CheckboxBase>
</template>

<script>
import CheckboxBase from './CheckboxBase'

export default {
  name: 'CheckboxItem',
  components: {
    CheckboxBase
  },
  props: {
    id: String | Object,
    text: String,
    checkedNames: Array,
    isDisabled: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    input: {
      get() {
        return this.checkedNames;
      },
      set(value) {
        this.$emit("update:checkedNames", value);
      }
    }
  }
}
</script>
