      selectedTradeType:[ 'Lease' ],
    }
  },
  mounted(){
    this.$store.dispatch('CHANGE_GNB_ACTION', false);
  },
  watch:{
    isActiveGnb(){
      if(this.isActiveGnb){
        this.isOpenList = false;
      }
    }
  },
  computed:{
    isActiveGnb(){
      return this.$store.state.gnb.isActiveGnb;
    }
  },
  methods:{
    onClickListToggle(){
      this.isOpenList = !this.isOpenList;
      if(this.isActiveGnb && this.isOpenList){
        this.$store.dispatch('CHANGE_GNB_ACTION', false);
      }
    }
  }
}
